<template>
    <v-card v-if="app.chatCardStatus" height="450" class="elevation-12" style="position: absolute;right:10px;bottom:100px;">
        <v-card-text class="text-center" style="padding:40px 20px 10px 20px">
            <div class="headline font-weight-black" color="white" style="font-size: 35px !important;">Contact me?</div>
            <div style="margin: 10px;">Message will be send to my Whatsapp directly,<br> I will reply to you ASAP.</div>
            <v-text-field v-model="app.email_address" label="Email" placeholder="enquiry@email.com" solo></v-text-field>
            <v-textarea v-model="app.message"  label="Message" placeholder="Something you want to tell me ..." solo></v-textarea>
            <v-btn large rounded outlined style="margin: 10px;" @click="send_message">Send</v-btn>
        </v-card-text>
    </v-card>
</template>

<script>
    const axios = require('axios');
    export default {
        name: "chatDialog",
        data: () => ({
            app: {}
        }),
        created () {
            this.app = this.$store.state.app
        },
        methods: {
            send_message () {
                if (this.app.email_address !== '' && this.app.message !== '') {
                    this.app.loading_status = true
                    axios.post(process.env.VUE_APP_LINEBOTPUSHMESSAE, {
                        "id": "U6d930ccf5a0a8be93d7816e6e3360bef",
                        "message": "收到由"+this.app.email_address+"傳來的訊息:\n"+this.app.message
                    }).then(res => {
                        this.app.loading_status = false
                        this.app.chatCardStatus = false
                        this.app.snackbarStatus = res.status
                        this.app.snackBarText = 'Message was delivered'
                    })
                }else {
                    this.app.snackbarStatus = true
                    this.app.snackBarColor = "error"
                    this.app.snackBarIcon = "error"
                    this.app.snackBarText = 'Please fill in the blank'
                }
            }
        }
    }
</script>

<style>
    .gen_color {
        background: linear-gradient(90deg, #69b7eb, #b3dbd3, #f4d6db);
    }
</style>